@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}
:root{
    /* --color-bg: #1f1f38; */
    --color-bg: black;
    /* --color-bg-variant: #2c2c6c; */
    --color-bg-variant: #648a91;
    /* --color-primary: #4db5ff; */
    --color-primary: white;
    --color-primary-varient: rgba(0, 143, 119, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);
    
    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-ms: 90%;
}
html{
    scroll-behavior: smooth;
}
::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    /* background: var(--color-bg); */
    background: #3a6186;  
    background: -webkit-linear-gradient(to right, #89253e, #3a6186);  
    background: linear-gradient(to right, #89253e, #3a6186); 
    color: var(--color-white);
}

/* ===== Genaral Styles */
.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}
h1, h2, h3, h4, h5{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}
section{
    margin-top: 4rem;
}
section > h2, section > h5{
    text-align: center;
    color: var(--color-light);
}

section > h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}
.text-light{
    color: var(--color-light);
}
a{
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover{
    color: var(--color-white);
}

.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}
.btn-primary{
    background: var(--color-primary);
    color:var(--color-bg);
    background-color: rgb(15, 207, 233);
    transition: var(--transition);
    border: none;
}
.btn-primary:hover{
    border: 0.5px solid white;
    background: transparent;
    color: rgb(17, 192, 236);
}
img{
    display: block;
    width: 100%;
    object-fit: cover;
}


/* ====== Media Query (Medium Devices) */

@media screen and (max-width: 1024px){
    .container{
        width: var(--container-width-md);
    } 
    section{
        margin-top: 6rem;
    }
}

/* ====== Media Query (Small Devices) */

@media screen and (max-width: 600px){
    .container{
        width: var(--container-width-sm);
    } 
    section > h2{
        margin-bottom: 62rem;
    }
}
